import PropTypes from 'prop-types'
import { Media, MeWrapper } from './styled'

const Me = ({ data }) => {
  return (
    <MeWrapper>
      <h1>{data.name}</h1>
      <h4>{data.headline}</h4>
      <h2>
        <Media>
          {data.media.map((site, i) => (
            <a key={i} href={site.href} target='_blank' rel='noreferrer'>
              <i className={`fa fa-${site.icon}`}></i>
            </a>
          ))}
        </Media>
      </h2>
    </MeWrapper>
  )
}

Me.propTypes = {
  id: PropTypes.string,
  data: PropTypes.object,
}


Me.ID = 'me'

export default Me
